.layout {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.header {
    background: transparent linear-gradient(93deg, #025fdb, #025fdb 35%, #194b86) 0 0 no-repeat padding-box;
    color: #EBF3FF;
}

.headerContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 10px;
}

.headerTitleContainer {
    display: flex;
    align-items: center;
    flex-grow: 1;
    justify-content: center;
    color: #EBF3FF;
    text-decoration: none;
}

.headerTitleImage {
    height: 40px;
    margin-right: 12px;
}

.headerTitle {
    font-weight: 600;
    margin: 0;
    text-align: center;
}

.headerLogo {
    height: 40px;
}
