.container {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.chatRoot {
    flex: 1;
    display: flex;
}

.chatContainer {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.chatEmptyState {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-height: 1024px;
    padding-top: 60px;
}

.chatEmptyStateTitle {
    font-size: 4rem;
    font-weight: 600;
    margin-top: 0;
    margin-bottom: 30px;
}

.chatEmptyStateSubtitle {
    font-weight: 600;
    margin-bottom: 10px;
}

@media only screen and (max-height: 780px) {
    .chatEmptyState {
        padding-top: 0;
    }

    .chatEmptyStateTitle {
        font-size: 3rem;
        margin-bottom: 0px;
    }
}

.chatMessageStream {
    flex-grow: 1;
    width: 100%;
    overflow-y: auto;
    padding-left: 24px;
    padding-right: 24px;
    display: flex;
    flex-direction: column;
    overflow-y: hidden;
    overflow-x: hidden;
    height: 76vh;
    padding-top: 2vh;
    overflow: scroll;
}

.chatMessageStream::-webkit-scrollbar {
    display: none;
}

.chatMessageGpt {
    margin-bottom: 20px;
    display: flex;
}

.chatMessageGptMinWidth {
    max-width: 500px;
    margin-bottom: 5px;
}

.chatInput {
    position: sticky;
    bottom: 0;
    flex: 0 0 100px;
    padding-top: 12px;
    padding-bottom: 24px;
    padding-left: 24px;
    padding-right: 24px;
    width: 100%;
    max-width: 1028px;
    background: #EBF3FF;
}

.loadingLogo {
    font-size: 28px;
}

.splitContainer {
    display: flex;
    width: 100%;
}

.chatSection {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: auto;
}

.pdfSection {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    border-left: 2px solid #ccc;
    background: #f8f8f8;
    position: relative;
}

.pdfViewer {
    width: 100%;
    height: 100%;
    border: none;
}

.welcomeScreen {
    align-items: center;
    width: 40%;
    align-self: center;
}

.welcomeTitle {
    color: #0C3058;
    font-weight: bold;
    text-align: center;
    margin-bottom: 40px;
    direction: rtl;
}

.welcomeText, .disclaimer {
    color: #0C3058;
    text-align: center;
    font-weight: 500;
    margin-bottom: 40px;
    direction: rtl;
}

.warningBox {
    background-color: #FFF9EC;
    color: #775508;
    border: 1px solid #775508;
    border-radius: 5px;
    padding: 10px;
    text-align: center;
    font-weight: 500;
    margin-bottom: 40px;
    direction: rtl;
}

.links {
    text-align: center;
    margin: 10px;
    margin-bottom: 40px;
}

.link {
    color: #0C3058;
    text-decoration: none;
    font-weight: 500;
    margin: 15px;
}

.welcomeButton {
    display: flex;
    height: 50px;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    padding: 10px;
    border-radius: 25px;
    margin-left: 10px;
    margin-right: 10px;
    border: none;
    cursor: pointer;
    margin: 5px;
    font-size: 18px;
    color: #0C3058;
    border: 3px solid #0C3058;
}

.questionMark {
    margin-right: 5px;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    border: 3px solid #0C3058;
    justify-content: center;
    text-align: center;
    margin-left: 5px;
    padding: 3px;
}

.welcomeButtonsContainer {
    display: flex;
    flex-direction: row;
}

.space {
    height: 80px;
}

.closePdfButton {
    position: absolute;
    top: 85px;
    right: 50px;
    width: 40px;
    height: 40px;
    background-color: transparent;
    border: 1px solid #DCDCDC;
    cursor: pointer;
    background-color: ghostwhite;
    border-radius: 25px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;
}

.closePdfButton::before {
    content: '';
    width: 18px;
    height: 2px;
    background-color: #202020;
    position: absolute;
    transform: rotate(45deg);
}

.closePdfButton::after {
    content: '';
    width: 18px;
    height: 2px;
    background-color: #202020;
    position: absolute;
    transform: rotate(-45deg);
}

.closePdfButton:hover {
    background-color: lightgray;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}
