.questionInputContainer {
    border-radius: 40px;
    box-shadow:
        0px 8px 16px rgba(0, 0, 0, 0.14),
        0px 0px 10px rgba(0, 0, 0, 0.12);
    height: 80px;
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    background: transparent;
    align-items: center;
}

.questionInputTextArea {
    width: 100%;
    line-height: 40px;
}

.questionInputButtonsContainer {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    transform: rotate(180deg);
}

.questionInputContainer div {
    border-radius: 40px;
}

.questionInputContainer textarea {
    height: 20px;
    font-size: 18px;
    padding: 20px;
    background: #EBF3FF;
    font-weight: 500;
}